/* Color Theme Swatches in Hex */
$Chalk-lettering-1-hex: #BF907E;
$Chalk-lettering-2-hex: #A63117;
$Chalk-lettering-3-hex: #F2F2F2;
$Chalk-lettering-4-hex: #595959;
$Chalk-lettering-5-hex: #0D0D0D;


header {
    margin: 10px;
    height: 300px;
    border-top-right-radius: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 -60px;
    filter: brightness(85%);

    .headerBorder {
        position: relative;
        top: 10px;
        left: 10px;
        border: 1px $Chalk-lettering-3-hex solid;
        height: 280px;
        width: calc(100% - 20px);
        border-top-right-radius: 10px;
    }
}

.izmir {
    background-image: url(../../images/Izmir.jpg);
}

.cap {
    background-image: url(../../images/cap.jpg);
}

.carTripToTurkey {
    background-image: url(../../images//Trips/CarTripToTurkey/ist.jpg);
    background-position: 0 -100px;
}

.alb {
    background-image: url(../../images/alb.jpg);
    background-position: 0 -100px;
}

@media (max-width: 1090px) {
    header {
        height: 200px;
        background-position: 0 -30px;

        .headerBorder {
            height: 180px;
            width: calc(100% - 20px);
        }
    }

    .alb,
    .carTripToTurkey {
        background-position: 0 -40px;
    }

    @media (max-width: 768px) {
        header {
            border-top-right-radius: 0;

            .headerBorder {
                border-top-right-radius: 0;
            }
        }
    }

    @media (max-width: 540px) {
        header {
            background-position: 0 0px;
        }

        .alb,
        .carTripToTurkey {
            background-position: 0 0px;
        }
    }
}